.ta {
  border-radius: 50%; 
  height: 12vh;
  left: 0;
  align-self: start;
  z-index: 1;
  position: fixed;
}

.nav0{
  top: 0.1rem;
  position: fixed;
}

* {
  box-sizing: border-box;
}

.bg{
  width: 100vw;
  height: 100vh;
  background-image: url(/ta-app/static/media/bg0.6da60f96.gif);
  background-size: cover;
  background-color: lawngreen;
}


.nav {
  display: inline-flex;
  position: relative;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  width: 99vw;
  height: 12vh;
  background-color: #fff;
  border-radius: 55px;
  box-shadow: 0 10px 40px rgba(159, 162, 177, .8);
}

.nav-item {
  color: #83818c;
  padding:4vw;
  text-decoration: none;
  transition: .3s;
  margin: 0 6px;
  z-index: 1;
  font-family: Georgia, serif;
  font-size: 3.5vh;
  font-weight: 700;
  position: relative;
}

.nav-item:before {
    content: "";
    position: absolute;
    bottom: -6px;
    left: 0;
    width: 100%;
    height: 29px;
    background-color: #64bdf0b0;
    border-radius: 8px 8px 0 0;
    opacity: 0;
    transition: .3s;
  }

.nav-item:not(.is-active):hover:before {
  opacity: 1;
  bottom: 0;
}


.nav-item:not(.is-active):hover { color: #333; }

.nav-indicator {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 4px;
  transition: .4s;
  height: 5px;
  z-index: 1;
  border-radius: 8px 8px 0 0;
}

@media (max-width: 580px) {
  .nav { overflow: auto; }
}

@media only screen and (max-width: 800px) {

  

  .nav-item
  {
    font-size: 3vw;
    padding: 2vw;
  }
  .nav , .ta{
    
    height: 9vh;
    
  }

  .nav-item:before{
    height: 9px;
  }

}

